<template>
    <div>
        <h2>Welcome To Our Web App</h2>
        <article class="row">
            <div class="col-sm-8 col-md-6 col-lg-4 col-xl-3 m-auto">
                <form>
                    <div class="form-group">
                        <label for="signupemail">Email: </label>
                        <input type="email" class="form-control" v-model="email" name="" id="signupemail" aria-describedby="emailHelpId" placeholder="">
                        <small id="emailHelpId" class="form-text text-muted">Help text</small>
                    </div>
                    <div class="form-group">
                        <label for="passwordsignup">Password:</label>
                        <input type="password" class="form-control" v-model="password" name="" id="passwordsignup" aria-describedby="emailHelpId" placeholder="">
                        <small id="emailHelpId" class="form-text text-muted">Help text</small>
                    </div>
                    <div class="form-group">
                        <input type="submit" value="SignUp" class="btn btn-primary" v-on:click.prevent="signup" >
                    </div>
                </form>
            </div>

        </article>
    </div>
</template>

<script>
import NavS from './nav.vue'
export default {
    components:{
        'app-nav':NavS
    },
    data () {
        return {
            email: '',
            password: ''
        }
    },
    methods:{
        signup: function(){
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
            .then((user)=>{
                alert('created'+ user)
                this.$router.replace('/')
            })
            .catch((error)=>{
                // Handle Errors here.
                alert('error' + error)  
            })
        }
    }
}
</script>

<style scoped>
.form-control{
    background-color: black !important;
    color: tomato;
    border: 1px solid steelblue;
}
label{
    color:cyan;
}
</style>
