<template>
    <div>
        <app-nav></app-nav>
        <h2>Login page</h2>
        <article class="row">
            <div class="col-sm-8 col-md-6 col-lg-4 col-xl-3 m-auto">
                <form action="" method="post">
                    <div class="form-group">
                        <label for="">Email: </label>
                        <input type="email" class="form-control" v-model="email" name="" id="" aria-describedby="emailHelpId" placeholder="">
                        <small id="emailHelpId" class="form-text text-muted">Help text</small>
                    </div>
                    <div class="form-group">
                        <label for="">Password:</label>
                        <input type="password" class="form-control" v-model="password" name="" id="" aria-describedby="emailHelpId" placeholder="">
                        <small id="emailHelpId" class="form-text text-muted">Help text</small>
                    </div>

                    <div class="form-group">
                        <input type="submit" value="SignIn" class="btn btn-primary" v-on:click.prevent="signin" >
                    </div>
                </form>
            </div>

        </article>
    </div>
</template>

<script>
import NavS from './nav.vue'

export default {
    components:{
        'app-nav':NavS
    },
     data () {
        return {
            email: '',
            password: ''
        }
    },
    methods:{
        signin: function(){
            firebase.auth().signInWithEmailAndPassword(this.email, this.password)
            .then((user)=>{
                alert('created'+user)
                this.$router.replace('/home')
            })
            .catch((error)=>{
                // Handle Errors here.
                alert('error'+error)  
            })
        }
    }
}
</script>

<style scoped>
.form-control{
    background-color: black !important;
    color: tomato;
    border: 1px solid steelblue;
}
label{
    color:cyan;
}
</style>
